import styled from "styled-components";
import Link from "next/link";
import "./OpenSlideBar.css";

const OpenSlideBar = props => {
  let drawerClasses = "side-drawer";
  if (props.show) {
    drawerClasses = "side-drawer open";
  }

  return (
    <nav className={drawerClasses}>
      <Ul>
        <li>
          <Link href="/blog">
            <A>BLOG</A>
          </Link>
        </li>
        <li>
          <Link href="/portfolio">
            <A>PORTFOLIO</A>
          </Link>
        </li>

        <li>
          <Link href="/wanderlust">
            <A>WANDERLUST: travel lover</A>
          </Link>
        </li>
        <li>
          <Link href="/video">
            <A>VIDEO</A>
          </Link>
        </li>
        <li>
          <Link href="/clients">
            <A>FOR CLIENTS</A>
          </Link>
        </li>
        <li>
          <Link href="/contacts">
            <A>CONTACTS</A>
          </Link>
        </li>
      </Ul>
    </nav>
  );
};

export default OpenSlideBar;

const Open = styled.div``;

const SideDrawer = styled.nav``;

const Ul = styled.ul`
  width: 70%;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
`;

const A = styled.a`
  color: #4a4a4a;
  text-decoration: none;
  line-height: 50px;
  cursor: pointer;
  &:hover {
    color: #a6a6ad;
  }
`;
